.container {
  margin: auto;
  width: 95%;
  min-height: 600px;
  margin-top: 120px;
}
.page-header {
  width: 100%;
  height: 100px;
  /* background-color: forestgreen; */
}
.page-title {
  line-height: 75px;
  color: forestgreen;
  font-size: 24pt;
}
.page-content {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  margin: auto;
  width: inherit;
  min-height: 550px;
  text-align: justify;
  font-weight: bold;
  font-size: 14pt;
  line-height: 50px;
}
h3 {
  text-align: left;
}
