body {
  background-color: rgba(2, 104, 19, 0.2);
  background-blend-mode: lighten;
  color: rgb(63, 62, 62);
  font-family: Arial, Helvetica, sans-serif;
}
.nav {
  list-style: none;
  margin: 0px;
  padding: 0px;
  width: 100%;
  background-color: #424242;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}
.nav li {
  display: inline-block;
  margin: 20px;
  font-size: 14pt;
  font-weight: bold;
}
.App a {
  color: white;
  text-decoration: none;
}
.App a:hover,
.App a:active {
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.letter {
  width: 100%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
