.project-card {
  margin: auto;
  margin: 0px 10px 50px 10px;
  width: 45%;
  min-width: 300px;
  padding-top: 0px;
  background-color: #333;
  border-radius: 20px;
  box-shadow: 5px 5px 10px #111;
  overflow: hidden;
  flex-grow: 1;
}
.project-card-header {
  width: 100%;
  color: #ccc;
}
.project-card-title {
  height: 100px;
  text-align: center;
  background-color: #222;
  font-size: 20pt;
  line-height: 100px;
}

.project-card-info {
  padding: 20px;
  width: 100%;
  overflow: hidden;
}
.project-card-info-title {
  color: red;
  font-size: 20pt;
  font-weight: bold;
}
.project-card-info p {
  word-wrap: wrap;
  margin-right: 40px;
  padding: 0px;
}
.project-card-slideshow {
  width: 100%;
}

.project-card-slideshow-nav {
  width: 100%;
  padding: 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.project-card-slideshow-nav button {
  width: 60px;
  height: 60px;

  background-color: #444;
  color: #aaa;
  border: none;
}
#slideId {
  width: 60px;
  height: 60px;
  text-align: center;
  font-size: 20pt;
  line-height: 60px;
  color: #aaa;
}
