.project-slide {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
.project-slide-image {
  width: 60%;
  flex-grow: 1;
}
.project-slide-image img {
  width: 100%;
  height: 100%;
}

.project-slide-info {
  width: 20%;
  min-width: 300px;
  min-height: 100%;
  background-color: #222;
  color: #ccc;
  overflow: scroll;
  flex-grow: 1;
}
.project-slide-info h2 {
  margin-left: 20px;
}
.project-slide-info p {
  padding: 20px;
}
